import dnd from 'shared/drag-n-drop/dnd.service';

export default angular.module('deskworks.inventory.membershipCategory.list', [
  'deskworks.inventory.membershipCategory.service',
  dnd.name,
])

  .controller('MembershipCategoryListCtrl', function($scope, $translate, state, dnd, helper, dwAlerts,
                                                     membershipCategories, membershipCategoryService) {
    const vm = this;

    state.setPageTitle($translate.instant('MEMBERSHIP_CATEGORIES.PAGE_TITLE'));

    vm.membershipCategories = membershipCategories;

    /**
     * Edit Membership Category handler
     */
    vm.edit = category => state.setPath('/membership-types/' + category.id);

    /**
     * New Membership Category handler
     */
    vm.new = () => state.setPath('/membership-types/new');

    /**
     * Drop handler
     */
    vm.onDrop = (srcList, srcIndex, targetList, targetIndex) => {
      const item = dnd.move(srcList, srcIndex, targetList, targetIndex);

      item.$spin = true;
      membershipCategoryService.update(item.id, { order: item.order, })
        .catch(err => dwAlerts.error(err, 'Failed to reorder membership category.'))
        .finally(() => delete item.$spin);

      return true; // true is required by dnd-callback of dnd-list
    };
  });
