import template from './printer-usage-free-copies.tpl.html';

/**
 * printerUsageFreeCopies directive - free copies configuration for Membership Product -> Free Copies
 */
export default angular.module('deskworks.printerUsageFreeCopies', [
  'deskworks.printerUsageTypeNames',
])

  .directive('printerUsageFreeCopies', function() {
    return {
      template,
      restrict: 'EAC',
      scope: true,
      controller: 'PrinterUsageFreeCopiesCtrl as $ctrl',
      bindToController: {
        types: '<freeCopiesSettings',
      },
    };
  })

  .controller('PrinterUsageFreeCopiesCtrl', function(printerUsageTypeNames) {
    const $ctrl = this;

    /**
     * $onInit
     */
    $ctrl.$onInit = () => {
      $ctrl.typeNames = printerUsageTypeNames;
    };
  });
