import reservationRulesDirective from 'shared/reservation-rules/reservation-rules.component';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';

export default angular.module('deskworks.inventory.reservationUnit.edit', [
  'deskworks.reservationCategory.service',
  reservationRulesDirective.name,
  ReservationUnit.name,
])

  .controller('InventoryReservationUnitEditCtrl', function($scope, $route, $translate, $window, dwAlerts,
                                                           state, helper, unit, categories, ReservationUnit) {
    const $ctrl = this;

    state.setPageTitle($translate.instant(
      unit.id === 'new' ? 'RESERVATION_UNITS.NEW.PAGE_TITLE' : 'RESERVATION_UNITS.EDIT.PAGE_TITLE'
    ));

    $ctrl.unit = unit;
    $ctrl.categories = categories;
    $ctrl.areaUnit = (window.dwLocales.find(i => i.id === $scope.currentLocale) || {}).area || 'ft';

    /**
     * Save Reservation Unit handler
     */
    $ctrl.saveReservationUnit = form => {
      if (helper.pointFirstInvalid('#reservation-unit-edit')) return;

      const data = helper.pick($ctrl.unit, [
        'id',
        'name',
        'reservationTypeId',
        'syncWithCalendar',
        'calendarId',
        'description',
        'area',
        'capacity',
        'expectedUsePerMonth',
      ]);
      data.area = data.area ? +data.area : null;
      data.capacity = data.capacity ? +data.capacity : null;
      data.expectedUsePerMonth = data.expectedUsePerMonth ? +data.expectedUsePerMonth : null;

      return ReservationUnit.save(state.getCurrentCenterId(), data)
        .then(result => {
          $ctrl.form.$setPristine();
          if ($ctrl.unit.id === 'new') return state.setPath('/reservation-units/' + result.id);
          $route.reload();
        })
        .catch(err => dwAlerts.error(err, 'Failed to save reservation unit.'));
    };

    /**
     * Remove Reservation Unit handler
     */
    $ctrl.removeReservationUnit = () => {
      if (confirm($translate.instant('RESERVATION_UNITS.CONFIRM_DELETE'))) {
        return ReservationUnit.delete(state.getCurrentCenterId(), $ctrl.unit.id)
          .then(() => state.setPath('/reservation-units'))
          .catch(err => dwAlerts.error(err, 'Failed to remove reservation unit.'));
      }
    };

    /**
     * Upload Photo handler
     */
    $ctrl.uploadPhoto = function($files, $invalidFiles) {
      if ($invalidFiles && $invalidFiles.length > 0) return dwAlerts.error($translate.instant('GLOBAL.PHOTO.INVALID_FORMAT'));
      if ($files && $files.length > 0) {
        $ctrl.spinUpload = true;
        return ReservationUnit.uploadPhoto(state.getCurrentCenterId(), $ctrl.unit.id, $files[0])
          .catch(err => dwAlerts.error(err, 'Failed to upload photo.'))
          .finally(() => { $ctrl.spinUpload = false; })
          .then(result => {
            $ctrl.unit.thumbnail = result.data.thumbnail ? result.data.thumbnail  + '?' + Date.now() : $ctrl.unit.thumbnail;
            $ctrl.unit.photo = result.data.photo + '?' + Date.now();
          });
      }
    };

    /**
     * Delete photo
     */
    $ctrl.deletePhoto = () => {
      if (!$window.confirm($translate.instant('GLOBAL.CONFIRM_DELETE_PHOTO'))) return;

      return ReservationUnit.deletePhoto({ centerId: state.getCurrentCenterId(), id: $ctrl.unit.id })
        .catch(err => dwAlerts.error(err, 'Failed to delete photo.'))
        .then(() => {
          $ctrl.unit.thumbnail = null;
          $ctrl.unit.photo = null;
        });
    };

    /**
     * Back button handler
     */
    $ctrl.back = () => state.setPath('/reservation-units');

    /**
     * 'Sync With Calendar' change handler
     */
    $ctrl.calSyncChange = () => {
      if (!$ctrl.unit.syncWithCalendar) $ctrl.unit.calendarId = null;
      $ctrl.calendarIdEnabled = $ctrl.unit.syncWithCalendar;
    };

    $scope.$watch('state.getCurrentCenterId()', (nv, ov) => { if (nv !== ov) state.setPath('/reservation-units'); });
  });
