import ReservationUnit from 'shared/services/inventory/reservation-unit.service';

export default angular.module('deskworks.inventory.reservationUnit.list', [
  'deskworks.reservationRules.optsWhoCanReserve',
  ReservationUnit.name,
])

  .controller('InventoryReservationUnitListCtrl', function($scope, $translate, state, helper,
                                                           reservationUnits, optsWhoCanReserve) {
    const vm = this;

    state.setPageTitle($translate.instant('RESERVATION_UNITS.PAGE_TITLE'));

    vm.reservationUnits = reservationUnits;

    vm.reservationUnits.forEach(unit => {
      unit.whoCanReserve = (helper.find(optsWhoCanReserve, unit.whoCanReserve) || {}).name;
    });

    /**
     * Edit Reservation Unit handler
     */
    vm.edit = reservationUnit => state.setPath('/reservation-units/' + reservationUnit.id);

    /**
     * New Reservation Unit handler
     */
    vm.new = () => state.setPath('/reservation-units/new');
  });
