import dnd from 'shared/drag-n-drop/dnd.service';

export default angular.module('deskworks.inventory.reservationCategories', [
  'deskworks.state',
  'deskworks.reservationCategory.service',
  dnd.name,
])

  .controller('InventoryReservationCategoriesCtrl', function($scope, $translate, state, dnd, dwAlerts,
                                                             reservationCategories, reservationCategoryService) {
    const vm = this;

    state.setPageTitle($translate.instant('RESERVATION_CATEGORIES.PAGE_TITLE'));

    vm.categories = reservationCategories;

    /**
     * Edit Reservation Category handler
     */
    vm.edit = reservationType => state.setPath('/reservation-types/' + reservationType.id);

    /**
     * New Reservation Category handler
     */
    vm.new = () => state.setPath('/reservation-types/new');

    /**
     * Drop handler
     */
    vm.onDrop = (srcList, srcIndex, targetList, targetIndex) => {
      const item = dnd.move(srcList, srcIndex, targetList, targetIndex);

      item.$spin = true;
      reservationCategoryService.update(state.getCurrentCenterId(), item.id, { order: item.order, })
        .catch(err => dwAlerts.error(err, 'Failed to reorder reservation category.'))
        .finally(() => delete item.$spin);

      return true; // true is required by dnd-callback of dnd-list
    };
  });
