import autofocusDirective from 'shared/autofocus/autofocus.directive';
import DiscountCode from './discount-code.service';

export default angular.module('DiscountCodeEditCtrl', [
  autofocusDirective.name,
  DiscountCode.name,
])

  .controller('DiscountCodeEditCtrl', function($scope, dwAlerts, helper, close, centerId, code, products, DiscountCode) {
    const $ctrl = this;

    $scope.close = close;
    $ctrl.code = code;
    $ctrl.products = products;
    $ctrl.types = ['$', '%'];

    $ctrl.getProducts = (prodId) => $ctrl.products.filter(p => !$ctrl.code.productIds.find(id => id === p.id && id !== prodId));

    /**
     * Add product
     */
    $ctrl.add = () => {
      $ctrl.code.productIds.push(null);
      $scope.form.$setDirty();
    };

    /**
     * Delete product
     */
    $ctrl.delete = (index) => {
      $ctrl.code.productIds.splice(index, 1);
      $scope.form.$setDirty();
    };

    /**
     * Save discount code
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#discountCodeEdit')) return;

      return DiscountCode.save($ctrl.code, centerId)
        .catch(err => dwAlerts.error(err, 'Failed to save discount code.'))
        .then(() => $scope.form.$setPristine())
        .then(() => close(true));
    };
  });
