import './reservation-category.scss';
import ffRichtextDirective from 'shared/ff-richtext/ff-richtext.directive';
import keese from 'shared/drag-n-drop/keese.service';

export default angular.module('deskworks.inventory.reservationCategory', [
  ffRichtextDirective.name,
  keese.name,
  'deskworks.helper',
  'deskworks.reservationCategory.service',
])

  .controller('InventoryReservationCategoryEditCtrl', function($window, $translate, keese, dwAlerts, state, helper,
                                                               reservationCategory, reservationCategories,
                                                               reservationCategoryService) {
    const vm = this;

    state.setPageTitle($translate.instant(
      reservationCategory.id === 'new' ? 'RESERVATION_CATEGORIES.NEW.PAGE_TITLE' : 'RESERVATION_CATEGORIES.EDIT.PAGE_TITLE'
    ));

    vm.category = reservationCategory;
    vm.hours = [0, 1, 2, 12, 24, 48, 72, 120, 168];
    vm.cancellation = [
      { notice: 'RESERVATION_CATEGORIES.NOTICE_NON_MEMBER', hint: 'RESERVATION_CATEGORIES.NOTICE_NON_MEMBER.HINT', model: vm.category.cancellationNonMemberNoticeHours },
      { notice: 'RESERVATION_CATEGORIES.NOTICE_MEMBER', hint: 'RESERVATION_CATEGORIES.NOTICE_MEMBER.HINT', model: vm.category.cancellationMemberNoticeHours },
    ];

    // Don't show photo placeholder
    if (vm.category.photos) {
      const photoPlaceholderIdx = helper.findIndex(vm.category.photos, null);
      if (photoPlaceholderIdx >= 0) {
        vm.category.photos.splice(photoPlaceholderIdx, 1);
      }
    }

    /**
     * Save Reservation Category handler
     */
    vm.save = form => {
      if (helper.pointFirstInvalid('#reservationCategory')) return;

      const data = {
        name: vm.category.name,
        description: vm.category.description,
        cancellationNonMemberNoticeHours: vm.cancellation[0].model,
        cancellationMemberNoticeHours: vm.cancellation[1].model,
      };

      let promise;
      if (vm.category.id === 'new') {
        data.order = keese.generate(reservationCategories[reservationCategories.length - 1] && reservationCategories[reservationCategories.length - 1].order);
        promise = reservationCategoryService.create(state.getCurrentCenterId(), data);
      } else {
        promise = reservationCategoryService.update(state.getCurrentCenterId(), vm.category.id, data);
      }

      return promise
        .then(result => {
          form.$setPristine();
          if (vm.category.id === 'new') return state.setPath('/reservation-types/' + result.id);
        })
        .catch(err => dwAlerts.error(err, 'Failed to save reservation category.'));
    };

    /**
     * Remove Reservation Category handler
     */
    vm.remove = () => {
      if ($window.confirm($translate.instant('RESERVATION_CATEGORIES.CONFIRM_DELETE'))) {
        return reservationCategoryService.delete(state.getCurrentCenterId(), vm.category.id)
          .then(() => state.setPath('/reservation-types'))
          .catch(err => dwAlerts.error(err, 'Failed to remove reservation category.'));
      }
    };

    /**
     * Back button handler
     */
    vm.back = () => state.setPath('/reservation-types');

    /**
     * Add/Edit Photo handler
     * @param $files - files to upload
     * @param $invalidFiles - files rejected by ngfPattern directive
     * @param photo - photo object, if missing - adding new photo, if present - updating existing photo
     */
    vm.uploadPhoto = ($files, $invalidFiles, photo) => {
      if ($invalidFiles && $invalidFiles.length > 0)
        return dwAlerts.error($translate.instant('RESERVATION_CATEGORIES.PHOTOS.INVALID_FORMAT'));

      if ($files && $files.length > 0) {
        let promise;

        if (photo) { // Update photo
          promise = reservationCategoryService.updatePhoto(state.getCurrentCenterId(), vm.category.id, photo.id, $files[0]);
          photo.spin = true;
        } else { // Create photo
          promise = reservationCategoryService.createPhoto(state.getCurrentCenterId(), vm.category.id, $files[0]);
          vm.spinAddPhoto = true;
        }

        return promise
          .then(result => {
            if (photo) { // Update photo
              photo.url = result.data.url;
            } else { // Create photo
              vm.category.photos.push({
                id: result.data.id,
                url: result.data.url,
              });
            }
          })
          .catch(err => dwAlerts.error(err, 'Failed to upload reservation category photo.'))
          .finally(() => {
            vm.spinAddPhoto = false;
            if (photo) photo.spin = false;
          });
      }
    };

    /**
     * Remove Photo handler
     */
    vm.removePhoto = photo => {
      if ($window.confirm($translate.instant('RESERVATION_CATEGORIES.PHOTOS.CONFIRM_DELETE'))) {
        return reservationCategoryService.deletePhoto(state.getCurrentCenterId(), vm.category.id, photo.id)
          .then(() => {
            const idx = helper.findIndex(vm.category.photos, photo.id);
            if (idx >= 0) vm.category.photos.splice(idx, 1);
          })
          .catch(err => dwAlerts.error(err, 'Failed to remove reservation category photo.'));
      }
    };
  });
