/**
 * autofocus directive
 */
export default angular.module('autofocusDirective', [])

  .directive('autofocus', function($timeout) {
    return {
      restrict: 'A',
      link: ($scope, $element, $attrs) => {
        if ($attrs.autofocus === 'false') return;
        const input = $element[0].parentNode.querySelector('input') || $element[0].parentNode.querySelector('select') || $element[0].parentNode.querySelector('button');
        if (input) $timeout(() => input.focus());
      },
    };
  });
