import 'textangularjs';
import 'textangularjs/dist/textAngular.css';
import './ff-richtext.scss';

/**
 * ffRichtext directive - wrapper for 3rd party textAngular
 */
export default angular.module('ffRichtext', ['textAngular'])

  .directive('ffRichtext', function(ffHelper) {
    return {
      restrict: 'E',
      scope: true,

      compile: function(tElement, tAttrs) {
        const input = angular.element('<text-angular></text-angular>');
        ffHelper.copyInnerAttrs(tElement, tAttrs, input);
        tElement.append(input);

        return this.link;
      },

      link: function($scope, $element, $attrs) {
        const content = angular.element($element[0].querySelector('[contenteditable]'));

        $scope.$watch('ffBlock.editMode', function(editMode) {
          if (angular.isUndefined(editMode)) return;
          $element.toggleClass('ff-read-only', !editMode);
          content.attr('contenteditable', editMode);
        });
      },
    };
  });
