export default angular.module('deskworks.inventory.membershipCategory.edit', [
  'keese',
  'deskworks.inventory.membershipCategory.service',
])

  .controller('MembershipCategoryEditCtrl', function($scope, $route, $translate, dwAlerts, keese, state, helper,
                                                     membershipCategories, membershipCategoryService) {
    const vm = this;

    vm.category = helper.find(membershipCategories, $route.current.params.membershipCategoryId) || {};
    if ($route.current.params.membershipCategoryId === 'new') vm.category.id = 'new';

    state.setPageTitle($translate.instant(
      vm.category.id === 'new' ? 'MEMBERSHIP_CATEGORIES.NEW.PAGE_TITLE' : 'MEMBERSHIP_CATEGORIES.EDIT.PAGE_TITLE'
    ));

    /**
     * Save membership category handler
     */
    vm.save = form => {
      if (helper.pointFirstInvalid('#membershipCategory')) return;

      const data = {
        name: vm.category.name,
        note: vm.category.note,
      };

      let promise;
      if (vm.category.id === 'new') {
        data.order = keese.generate(membershipCategories[membershipCategories.length - 1] && membershipCategories[membershipCategories.length - 1].order);
        promise = membershipCategoryService.create(data);
      } else {
        promise = membershipCategoryService.update(vm.category.id, data);
      }

      return promise
        .catch(err => dwAlerts.error(err, 'Failed to save membership category.'))
        .then(result => {
          form.$setPristine(true);
          if (vm.category.id === 'new') {
            return state.setPath('/membership-types/' + result.id);
          }
        });
    };

    /**
     * Remove membership category handler
     */
    vm.remove = () => {
      if (confirm($translate.instant('MEMBERSHIP_CATEGORIES.CONFIRM_DELETE'))) {
        return membershipCategoryService.delete(vm.category.id)
          .catch(err => dwAlerts.error(err, 'Failed to remove membership category.'))
          .then(() => state.setPath('/membership-types'))
      }
    };

    /**
     * Back button handler
     */
    vm.back = () => state.setPath('/membership-types');
  });
