import dwModal from 'shared/dw-modal/dw-modal.service';
import DiscountCode from './discount-code.service';
import DiscountCodeEditCtrl from './discount-code-edit.controller';
import discountCodeEditTpl from './discount-code-edit.tpl.html';

export default angular.module('deskworks.DiscountCodesCtrl', [
  dwModal.name,
  DiscountCode.name,
  DiscountCodeEditCtrl.name,
  'deskworks.inventory.product.service',
])

  .controller('DiscountCodesCtrl', function($scope, $translate, state, dwModal, dwAlerts, DiscountCode,
                                            productService) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('DISCOUNT_CODES.PAGE_TITLE'));

    $ctrl.codes = [];

    $ctrl.params = {
      centerId: state.getCurrentCenterId(),
      offset: 0,
      limit: 50,
    };

    /**
     * onInit
     */
    $ctrl.onInit = () => {
      $ctrl.fetch();
      productService.getProducts()
        .catch(err => dwAlerts.error(err, 'Failed to query products.'))
        .then(products => {
          $ctrl.products = products.filter(p => p.type === 'membership');
        })
    };

    /**
     * New discount code
     */
    $ctrl.new = () => {
      const code = {
        type: '$',
        duration: 1,
        startDate: moment().startOf('date'),
        endDate: moment().startOf('date').add(1, 'month'),
        productIds: [],
      };

      return $ctrl.edit(code)
        .then(result => {
          if (result) $ctrl.codes.unshift(code);
          sortCodes();
        });
    };

    /**
     * Edit discount code
     * @param code
     */
    $ctrl.edit = code => {
      const copy = { ...code };

      return dwModal.show({
        scope: $scope,
        template: discountCodeEditTpl,
        controller: 'DiscountCodeEditCtrl as $ctrl',
        inputs: { centerId: $ctrl.params.centerId, code: copy, products: $ctrl.products },
      })
        .then(modal => modal.close)
        .then(result => {
          if (result) {
            angular.extend(code, copy);
            sortCodes();
          }
          return result;
        });
    };

    /**
     * Delete discount code
     * @param code
     */
    $ctrl.delete = code => DiscountCode.delete(code.id)
      .catch(err => dwAlerts.error(err, 'Failed to delete discount code.'))
      .then(() => $ctrl.codes.splice($ctrl.codes.indexOf(code), 1));

    /**
     * Fetch discount codes
     */
    $ctrl.fetch = () => {
      if ($ctrl.eof || $ctrl.fetching) return $ctrl.fetching;

      $ctrl.fetching = DiscountCode.query($ctrl.params)
        .catch(err => dwAlerts.error(err, 'Failed to query discount codes.'))
        .finally(() => { $ctrl.fetching = null; })
        .then(codes => {
          $ctrl.codes = $ctrl.codes.concat(codes);
          sortCodes();
          $ctrl.params.offset = $ctrl.codes.length;
          $ctrl.eof = codes.length < $ctrl.params.limit;
        });

      return $ctrl.fetching;
    };

    /**
     * Bubble up active codes
     */
    function sortCodes() {
      const today = moment().endOf('day');
      const active = $ctrl.codes.filter(code => today.isSameOrBefore(code.endDate));
      const expired = $ctrl.codes.filter(code => today.isAfter(code.endDate));

      $ctrl.codes = active.concat(expired);
      $ctrl.firstExpiredIdx = active.length;
    }

    $ctrl.onInit();
  });
