import 'shared/printer-usage-free-copies/printer-usage-free-copies.directive';

export default angular.module('deskworks.inventory.product.edit', [
  'deskworks.inventory.product.service',
  'deskworks.inventory.optsCountCheckInsPer',
  'deskworks.printerUsageFreeCopies',
])

  .controller('ProductEditCtrl', function($scope, $q, $timeout, $window, $translate, dwAlerts, state, helper,
                                          product, settings, productService, optsPassMin, optsPassMax,
                                          optsCountCheckInsPer, productCategories) {
    const vm = this;

    state.setPageTitle($translate.instant(product.id ? 'PRODUCTS.EDIT.PAGE_TITLE' : 'PRODUCTS.NEW.PAGE_TITLE'));

    vm.settings = settings;
    vm.product = product;
    vm.optsPassMin = optsPassMin;
    vm.optsPassMax = optsPassMax;
    vm.optsCountCheckInsPer = optsCountCheckInsPer;
    vm.productCategories = productCategories.filter(cat => cat.code !== '0-00');
    vm.productCategories.forEach(cat => { cat.fullName = helper.join([cat.name, '[' + cat.code + ']'], ' '); });

    vm.passMinUnit = settings.minPassInPercent ? 'pass' : 'time';
    if (!settings.minPassInPercent) {
      vm.optsPassMin = angular.copy(vm.optsPassMin);
      vm.optsPassMin.forEach(opt => { opt.name += ' hr'; });
    }

    // Init passProducts
    vm.product.options.passProducts = [];
    productService.getProducts().then(products => {
      if (angular.isArray(products)) {
        products.forEach(product => {
          if (product.type === 'rental') {
            vm.product.options.passProducts.push(product);
          }
        });
        vm.product.passProduct = helper.find(vm.product.options.passProducts, vm.product.passProductId);
      }
    });

    /**
     * Save Product handler
     */
    vm.save = form => {
      if (helper.pointFirstInvalid('#productEdit')) return;

      let promise, isNew = false;
      if (vm.product.id) {
        promise = $q.all([
          productService.updateProduct(vm.product)
            .catch(err => dwAlerts.error(err, 'Failed to save product.')),
          $q.when(vm.saveQuickBooks && vm.saveQuickBooks()),
          $q.when(vm.saveDoorflow && vm.saveDoorflow()),
          $q.when(vm.saveDeskworksSweep && vm.saveDeskworksSweep().catch(angular.noop)),
        ]);
      } else {
        isNew = true;
        promise = productService.createProduct(vm.product)
          .catch(err => dwAlerts.error(err, 'Failed to save product.'))
          .then(result => (vm.product.id = result.id, result))
          .then(result => $timeout().then(() => result))
          .then(result => $q.when(vm.saveDeskworksSweep && vm.saveDeskworksSweep().catch(angular.noop)).then(() => result));
      }

      return promise
        .then(result => {
          form.$setPristine(true);
          if (isNew) return state.setPath('/products/' + result.id);
        });
    };

    /**
     * Remove Product handler
     */
    vm.remove = () => {
      if (confirm($translate.instant('PRODUCTS.CONFIRM_DELETE'))) {
        return productService.deleteProduct(vm.product.id)
          .catch(err => dwAlerts.error(err, 'Failed to remove product.'))
          .then(() => state.setPath('/products'));
      }
    };

    /**
     * Back button handler
     */
    vm.back = () => state.setPath('/products');

    /**
     * Add PriceList handler
     */
    vm.addPriceList = () => {
      const id = helper.uid();
      vm.product.priceLists.push({
        id: id,
        priceListsId: vm.product.options.priceLists[0].id,
        priceList: vm.product.options.priceLists[0],
        price: '',
        passQty: 0,
      });

      helper.focus('#productPriceList-' + id + ' select:first-of-type');
    };

    /**
     * Save PriceList handler
     */
    vm.savePriceList = (priceList, form) => {
      if (helper.pointFirstInvalid('#productPriceList-' + priceList.id)) return;

      const data = {
        priceListId: priceList.priceList.id,
        price: priceList.price,
      };

      if (vm.product.isCreditable) {
        data.reservationCredit = priceList.reservationCredit;
      }

      if (vm.product.hasPasses) {
        data.passQty = priceList.passQty;
      }

      return (helper.isUid(priceList.id) ? productService.createPriceList(vm.product.id, data) : productService.updatePriceList(vm.product.id, priceList.id, data))
        .catch(err => dwAlerts.error(err, 'Failed to save price list.'))
        .then(result => {
          if (helper.isUid(priceList.id)) priceList.id = result.id;
          form.$setPristine(true);
          return result;
        });
    };

    /**
     * Remove PriceList handler
     */
    vm.removePriceList = (priceList, index) => {
      if (helper.isUid(priceList.id)) {
        vm.product.priceLists.splice(index, 1);
      } else {
        return productService.deletePriceList(vm.product.id, priceList.id)
          .catch(err => dwAlerts.error(err, 'Failed to remove price list.'))
          .then(result => {
            vm.product.priceLists.splice(index, 1);
            return result;
          });
      }
    };

    /**
     * Get direct URL to Member Sign Up wizard
     */
    vm.getMemberSignUpUrl = () => {
      const origin = window.location.origin;
      const centerId = state.getCurrentCenterId();
      const productId = vm.product.id;
      return `${origin}/member-sign-up-landing?center=${centerId}&productId=${productId}`;
    };
  });
