import dwSelectCenterDirective from 'shared/dw-controls/dw-select-center/dw-select-center.directive';

export default angular.module('deskworks.inventory.priceList.list', [
  'deskworks.inventory.priceList.service',
  dwSelectCenterDirective.name,
])

  .controller('PriceListListCtrl', function($rootScope, $scope, $timeout, $route, $translate, dwAlerts, state,
                                            helper, priceLists, centers, priceListService, lazyLoadReports) {
    const vm = this;

    state.setPageTitle($translate.instant('PRICE_LISTS.PAGE_TITLE'));

    vm.centers = centers;
    vm.dstCenterId = state.getCurrentCenterId();
    vm.priceLists = priceLists;
    vm.priceLists.forEach(pl => { pl.include = true; });
    vm.editAfterCloning = true;
    vm.printCollapsed = true;

    /**
     * Edit price list handler
     */
    vm.edit = pl => state.setPath('/pricelists/' + pl.id);

    /**
     * Clone price list handler
     */
    vm.clone = pl => {
      if (vm.centers.length > 1 && !vm.cloning) {
        vm.cloning = pl;
        return;
      }

      return priceListService.clonePricelist(state.getCurrentCenterId(), pl.id, vm.dstCenterId)
        .then(result => {
          if (vm.editAfterCloning) {
            state.setCurrentCenterId(vm.dstCenterId);
            return state.setPath('/pricelists/' + result.id);
          } else {
            $route.reload();
            return $timeout(5000);
          }
        })
        .catch(err => dwAlerts.error(err, 'Failed to copy price list.'));
    };

    /**
     * New price list handler
     */
    vm.new = () => state.setPath('/pricelists/new');

    /**
     * Toggle Print price lists collapse
     */
    vm.togglePrint = () => lazyLoadReports
      .then(() => { vm.printCollapsed = !vm.printCollapsed; });

    // Due to route.reloadOnSearch: false
    $rootScope.$on('CHANGE-CENTER', () => {
      $route.reload();
    });
  });
