import './product-list.scss';

export default angular.module('deskworks.inventory.product.list', [
  'deskworks.inventory.product.service',
  'deskworks.billing.optsBillingFrequency',
])

  .controller('ProductListCtrl', function($scope, $translate, $timeout, state, helper, lazyLoadReports,
                                          products, membershipCategories, reservationCategories,
                                          productService, optsProductType, optsBillingFrequency) {
    const vm = this;

    state.setPageTitle($translate.instant('PRODUCTS.PAGE_TITLE'));

    vm.collapsed = true;
    vm.printCollapsed = true;

    vm.products = products;
    vm.productTypes = angular.copy(optsProductType); // deep-clone productTypes as we are going to modify it
    vm.productTypes.forEach(productType => { productType.products = []; });

    vm.customProducts = {
      id: 'custom',
      name: $translate.instant('PRODUCTS.LIST.CUSTOM'),
      products: [],
    };

    // Sort products by types
    products.forEach(product => {
      if (product.membershipTypeId) {
        const category = membershipCategories.find(c => c.id === product.membershipTypeId) || { name: product.membershipTypeId };
        product.membershipCategoryName = category.name;
      }
      if (product.passProductId) {
        const passProduct = products.find(p => p.id === product.passProductId) || { name: product.passProductId };
        product.passProductName = passProduct.name;
      }
      if (product.reservationTypeId) {
        const category = reservationCategories.find(c => c.id === product.reservationTypeId) || { name: product.reservationTypeId };
        product.reservationCategoryName = category.name;
      }
      if (product.billingFrequency) {
        product.billingFrequency = optsBillingFrequency.find(o => o.id === product.billingFrequency) || { name: product.billingFrequency };
      }

      const type = helper.find(vm.productTypes, product.type);
      if (product.custom) {
        vm.customProducts.products.push(product);
      } else if (type) {
        type.products.push(product);
      } else {
        console.warn('Product of unknown type:', product);
      }
    });

    /**
     * Edit product handler
     */
    vm.edit = product => state.setPath('/products/' + product.id);

    /**
     * New product handler
     */
    vm.new = () => state.setPath('/products/new');

    /**
     * Toggle Print Inventory List collapse
     */
    vm.togglePrint = () => lazyLoadReports
      .then(() => { vm.printCollapsed = !vm.printCollapsed; });
  });
