export default angular.module('deskworks.inventory.priceList.edit', [
  'deskworks.inventory.priceList.service',
])

  .controller('PriceListEditCtrl', function($scope, $rootScope, $filter, $timeout, $translate, dwAlerts, state, helper,
                                            priceList, priceListService, optsProductType) {
    const vm = this;

    state.setPageTitle($translate.instant(
      priceList.id === 'new' ? 'PRICE_LISTS.NEW.PAGE_TITLE' : 'PRICE_LISTS.EDIT.PAGE_TITLE'
    ));

    vm.priceList = priceList;

    vm.useForOptions = [
      { id: 'members', name: $translate.instant('PRICE_LISTS.USE_FOR.MEMBERS') },
      { id: 'nonMembers', name: $translate.instant('PRICE_LISTS.USE_FOR.NON_MEMBERS') },
      { id: 'none', name: $translate.instant('PRICE_LISTS.USE_FOR.NONE') },
    ];

    vm.priceList.useFor = helper.find(vm.useForOptions, vm.priceList.useFor);

    // Need to deep-clone productTypes as we are going to modify it
    vm.productTypes = angular.copy(optsProductType);
    vm.productTypes.forEach(productType => {
      productType.products = [];
      productType.options = [];
    });

    vm.priceList.options.products.forEach(product => {
      const type = helper.find(vm.productTypes, product.type);
      if (type) type.options.push(product);
      else console.warn('Product option of unknown type:', product);
    });

    // Transform product.productId to product.product reference to options.products elements, sort products by types
    vm.priceList.products.forEach(product => {
      product.product = helper.find(vm.priceList.options.products, product.productId);
      product.price = $filter('price')(product.price);
      if (angular.isDefined(product.reservationCredit)) {
        product.reservationCredit = $filter('price')(product.reservationCredit);
      }
      if (product.passQty === null) {
        product.passQty = 0;
      }

      const type = helper.find(vm.productTypes, product.product.type);
      if (type) type.products.push(product);
      else console.warn('Product of unknown type:', product);
    });

    /**
     * Remove price list handler
     */
    vm.remove = () => {
      if (confirm($translate.instant('PRICE_LISTS.CONFIRM_DELETE'))) {
        return priceListService.deletePricelist(state.getCurrentCenterId(), vm.priceList.id)
          .then(() => state.setPath('/pricelists'))
          .catch(err => dwAlerts.error(err, 'Failed to remove price list.'));
      }
    };

    /**
     * Save price list handler
     */
    vm.save = () => {
      if (helper.pointFirstInvalid('#priceListEdit')) return;

      const data = {
        name: vm.priceList.name,
        visibleForVisitors: vm.priceList.visibleForVisitors,
        useFor: vm.priceList.useFor ? vm.priceList.useFor.id : vm.priceList.useFor,
        isArchived: vm.priceList.isArchived,
      };

      let promise;
      if (vm.priceList.id === 'new') {
        promise = priceListService.createPricelist(state.getCurrentCenterId(), data);
      } else {
        promise = priceListService.updatePricelist(state.getCurrentCenterId(), vm.priceList.id, data);
      }

      return promise
        .then(result => {
          vm.priceList.date = moment();
          vm.form.$setPristine();
          if (vm.priceList.id === 'new') return state.setPath('/pricelists/' + result.id);
        })
        .catch(err => dwAlerts.error(err, 'Failed to save price list.'));
    };

    /**
     * Back button handler
     */
    vm.back = () => state.setPath('/pricelists');

    /**
     * Add Product handler
     */
    vm.addProduct = productType => {
      if (!productType.options.length) return dwAlerts.error($translate.instant('PRICE_LISTS.NO_PRODUCTS_ERROR'));

      const data = {
        id: 'new',
        productId: productType.options[0].id,
        product: productType.options[0],
        price: '',
        passQty: 0,
      };
      productType.products.push(data);

      // Focus on new product
      $timeout(() => {
        let last = document.querySelector('#productType-' + productType.id + ' form:last-of-type');
        if (last) {
          last = last.querySelector('select:first-of-type');
          if (last) last.focus();
        }
      });
    };

    /**
     * Save Product handler
     */
    vm.saveProduct = (product, form, index) => {
      if (helper.pointFirstInvalid('#productForm-' + index)) return;

      const data = {
        productId: product.product.id,
        price: product.price,
      };

      if (product.product.isCreditable) data.reservationCredit = product.reservationCredit;

      if (product.product.hasPasses) data.passQty = product.passQty;

      let promise;
      if (product.id === 'new') {
        promise = priceListService.createProduct(state.getCurrentCenterId(), vm.priceList.id, data);
      } else {
        promise = priceListService.updateProduct(state.getCurrentCenterId(), vm.priceList.id, product.id, data);
      }

      return promise
        .then(result => {
          if (product.id === 'new') product.id = result.id;
          form.$setPristine();
        })
        .catch(err => dwAlerts.error(err, 'Failed to save product.'));
    };

    /**
     * Remove product handler
     */
    vm.removeProduct = (product, index, productType) => {
      if (product.id === 'new') {
        productType.products.splice(index, 1);
      } else {
        if (confirm($translate.instant('PRICE_LISTS.PRODUCTS.CONFIRM_DELETE'))) {
          return priceListService.deleteProduct(state.getCurrentCenterId(), vm.priceList.id, product.id)
            .then(() => productType.products.splice(index, 1))
            .catch(err => dwAlerts.error(err, 'Failed to remove product.'));
        }
      }
    };

    /**
     * Redirect to list of price lists on changing current center
     */
    $scope.$watch('state.getCurrentCenterId()', (nv, ov) => { if (nv !== ov) state.setPath('/pricelists'); });
  });
