export default angular.module('deskworks.DiscountCode', [
  'ngResource',
  'datesDeserializer',
  'deskworks.config',
  'deskworks.helper',
])

  .factory('DiscountCode', function($resource, $timeout, $filter, datesDeserializer, deskworksConfig, helper) {
    const desDates = datesDeserializer(['startDate', 'endDate']);
    const datesSerializer = (obj, keys) => keys.reduce((res, key) => (res[key] = $filter('date')(obj[key], 'yyyy-MM-dd'), res), {});
    const serDates = obj => datesSerializer(obj, ['startDate', 'endDate']);

    const res = $resource(deskworksConfig.getApiAbsUrl('/discount-codes/:id'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [desDates] },
      update: { method: 'PUT' },
    });

    return {
      /**
       * @param params.centerId
       * @param params.offset
       * @param params.limit
       */
      query: params => res.query(helper.pick(params, ['centerId', 'offset', 'limit'])).$promise,

      save: (code, centerId) => {
        const data = { ...code, ...serDates(code, serDates(code)) };
        if (data.id) return res.update({ id: data.id }, data).$promise;
        return res.save({ centerId }, data).$promise
          .then(result => { code.id = result.id; });
      },

      delete: (id) => {
        return res.delete({ id }).$promise;
      }
    };
  });
