export default angular.module('deskworks.ProductCategoriesCtrl', [
  'debounce',
  'deskworks.ProductCategory',
])

  .controller('ProductCategoriesCtrl', function($scope, $translate, dwAlerts, state, helper, debounce,
                                                categories, ProductCategory) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('PRODUCT_CATEGORIES.PAGE_TITLE'));

    $ctrl.categories = categories;

    /**
     * Save handler
     */
    $ctrl.save = debounce(save, 1000);
    function save(category, form, index) {
      if (form.$invalid) return;

      category.$$spin = true;
      return ProductCategory.save(category)
        .catch(err => dwAlerts.error(err, 'Failed to save product category.'))
        .finally(() => { category.$$spin = false; });
    }

    /**
     * New handler
     */
    $ctrl.new = () => $ctrl.categories.push({});

    /**
     * Delete handler
     */
    $ctrl.delete = (category, index) => {
      if (category.id) { // existing category
        if (!confirm($translate.instant('PRODUCT_CATEGORIES.CONFIRM_DELETE'))) return;
        category.$$spin = true;
        return ProductCategory.delete(category.id)
          .catch(err => dwAlerts.error(err, 'Failed to delete product category.'))
          .then(() => $ctrl.categories.splice(index, 1))
          .finally(() => { category.$$spin = false; });
      } else { // new category
        $ctrl.categories.splice(index, 1);
      }
    };
  });
